/* -------- visual_main -------- */
.visual_main {
  position: relative;
  //height: 820px;
  width: 100%;
  height: 100vh;

  img {
    display: block;
    width: 100%;
    height: 100vh;
    object-fit: cover;
  }

  .vmSwiper {
    width: 100%;
    height: 100%;

    .swiper-slide {
      text-align: center;
      font-size: 18px;
      background: #fff;
      display: flex;
      justify-content: center;
      align-items: center;
    }


    .swiper-pagination {
      .swiper-pagination-bullet {
        width: 10px;
        height: 10px;
      }
    }
  }

}

body {
  background: #eee;
  font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  color: #000;
  margin: 0;
  padding: 0;
}